@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@250&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Roboto Mono' ,-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  width: 100vw;
  height: 100vh;

  overflow-y: hidden;
}

#root{
  width: 100%;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* .point{
  width: 200px;
  height: 400px;
} */

.header{
  width: 100%;
  padding: 0.5em 1em;
  background-color: blue;
  color: white;
}

.header .user-count{
  text-align: right;
}
.user-count{
  display: flex;
  justify-content:flex-end;
  align-items: center;
}
.footer{
  text-align: center;
  background-color: blue;
  padding: 0.4em 0.3em;
  width: 100%;
  font-size: smaller;
  color: white;
}
.nav{
  position: relative;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: black;
  width: 100%;
  height: 100%;
  color: white;
  z-index: 1000;
  transition: 500ms ease-in;
  transform: translateY(100%);
}

.nav ul {
  display: flex;
  flex-direction: column;
  grid-gap: 2em;
  gap: 2em;
}

.nav ul li{
  list-style: none;
  font-size: 1.5em;
  cursor: pointer;
}

.nav ul li a{
  color: white;
  text-decoration: none;
}

.navCloseBtn {
  position: absolute;
  top: 10%;
  right: 20%;
  font-size: 1.5em;
  cursor: pointer;
}

.nav.show{
  transform: translateY(0);
}
.navCloseBtn p{
  display: none;
}

.app{
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}

main{
  width: 100%;
  height: 100%;
}


.menu{
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 3em;
  bottom: 5%;
  left: 3%;
  z-index: 1000;
  cursor: pointer;
}

.menu i{
  font-size: 1em;
  text-align: center;
}

.menu p{
  font-size: 18px;
  animation-name: blink;
  animation-duration: 3s ;
  animation-iteration-count: infinite;
}


@keyframes blink {
  0% {opacity: 1;}
  50% {opacity: 0;}
}


@media screen and (min-width: 700px) {
  .menu{
    display: inline;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    text-align: right;
    top: 15%;
    right: 5%;
  }
}
.loading{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
.about{
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  padding: 0.5em 1em;
  background-color: lightblue;
  color: black;
}

.about > div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0.5em;
}

.space > p{
  margin-top: 1rem;
}

.about a{
  color: red;
  text-decoration: none;
  border: 1px solid black;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 1em;
  width: 13ch;
  padding: 0.2em;
  text-align: center;
}

.about .about-head{
  display: flex;
  justify-content: center;
  text-align: center;
}

.about footer{
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
  padding: 0.5em 2em;
  text-align: center;
}
.signup{
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: smaller;
  padding: 1em;
  height: 100%;
  width: 100%;
  overflow-y: scroll;
}

.signup .signup-head{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.signup p{
  text-align: center;
}

.signup .map-wrapper{
  width: 100%;
  height: 100%;
}
.signup .google-signin{
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 1em;
  grid-gap: 0.5em;
  gap: 0.5em;
}


.signup .form-map{
  position: relative;
  background-color: lightblue;
  width: 100%;
  height: 150px;
}

.signup .leaflet-container{
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  left: 0;
}

.signup .form-info{
  padding-top: 1em;
}

.signup form > div{
  display: flex;
  flex-direction: column;
  padding: 0.1em;
}

.signup input{
  padding: 0.2em;
}

.signup .submit-btn{
  padding: 1em 0;
}

.signup .back{
  cursor: pointer;
}

.signup .back a{
  color: red;
}

.signup .searchPlace{
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 100%;
  grid-gap: 0.3em;
  gap: 0.3em;
  background-color: rgb(25, 72, 160);
  color: white;
  padding: 0.2em 0.3em;
}

.signup .searchPlace input{
  width: 30ch;
  border: none;
  border-bottom: 1px solid;
  padding: 0 0.5em;
}

.signup .searchPlace i{
  font-size: 1.8em;
  cursor: pointer;
}



.signup .submit-btn{
  display: flex;
  flex-direction: row;
  justify-content: center;
  grid-gap: 1em;
  gap: 1em;
}

.signup .submit-btn > button{
  width: 10ch;

}

.signup .footer{
  background-color: white;
  font-size: larger;
}


@media screen and (min-width: 700px) {
  .signup{
    font-size: 1rem;
  }

  .signup .map-wrapper{
    width: 60%;
    min-height: 100%;
  }
}


@media screen and ( min-width: 1000px){
  .signup .map-wrapper{
    width: 50%;
    min-height: 100%;
  }
}
.login{
  height: 100%;

  background-color: lightblue;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content:center;
}

.login .login-head{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1em 1.5em;
}

.login form{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 1em;
  grid-gap: 0.8em;
  gap: 0.8em;
}

.login form > div{
  display: flex;
  flex-direction: column;
  width: 100%;
}

.login form div > input{
  padding: 0.3em 1em;
  width: 30ch;
}

.login button{
  background-color: blue;
  padding: 0.4em 1em;
  font-size: larger;
  color: white;
  border-radius: 5px;
  cursor: pointer;
}

.login .home{
  padding: 1em;
}

.login .login-signup{
  text-align: center;
}



